/**
 *
 * @function masterCook
 *
 * @description Add or remove automatically an array of classes in a Element
 *
 * @param {el} HTML Element @required
 * @param {classList} Array @required
 * @param {method} String add or remove @default add
 *
 * @return undefined
 *
 */

export default function (el, classList, method = 'add') {
  if (!el) throw new Error('masterCook needs an element to manage')
  if (!classList) throw new Error('masterCook needs a classList to work with element')

  for (let classe of classList) {
    if (classe) {
      try {
        el.classList[method](classe)
      } catch (e) {
        console.warn(e)
      }
    }
  }
}



